<template>
	<view>
		<view>
			<view>
				<navbar></navbar>
			</view>
			<view class="identity" style="display: flex;flex-direction: row;">
				<button @click="identity" style="width: 50%">鉴别</button>
				<button @click="postimg" style="width: 50%">上传</button>
			</view>
			<view class="checkpost">
				<image v-for="(item,index) in imagepath" :src="item"></image>
			</view>
		</view>
		<u-tabbar :list="tabbar" :mid-button="true"></u-tabbar>
	</view>
</template>

<script>
	import navbar from '../../components/navbar.vue'
	export default {
		components: {
			navbar,
		},
		data() {
			return {
				tabbar: '',
				imagepath: [],
			}
		},
		methods: {
			identity() {
				uni.uploadFile({
					url: 'http://39.103.136.247:5088/api/image',
					filePath: imagepath[0],
					name: 'image', //后台接收字段名
					success: (res) => {
						if (res.data.code == 200) {
							console.log('请求成功_______________', res)
							uni.showToast({
								icon: 'none',
								title: '提交成功',
								success: (res) => {
									setTimeout(() => {
										uni.navigateBack({
											delta: 1
										})
									}, 1500)
								}
							})
						}

					},
					fail: (err) => {
						console.log('请求失败_______________', err)
					}
				})
			},
			postimg() {
				uni.chooseImage({
					count: 1, //图片数
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['camera', 'album'], //相机或相册选择
					success: res => {
						this.imagepath = res.tempFilePaths
					}
				})
			},
			onShow() {
				uni.hideTabBar({
					animation: false
				})
			},
			onLoad() {
				this.tabbar = [{
						iconPath: "/static/icon/component.png",
						selectedIconPath: "/static/icon/component_select.png",
						text: '首页',
						pagePath: "/pages/home/home"
					},
					{
						iconPath: "/static/icon/component.png",
						selectedIconPath: "/static/icon/component_select.png",
						text: '鉴别',
						pagePath: "/pages/identification/identification"
					},
					{
						iconPath: "/static/icon/1.png",
						selectedIconPath: "/static/icon/1-1.png",
						text: '拍照',
						midButton: true,
						pagePath: "/pages/photograph/photograph",
					},
					{
						iconPath: "/static/icon/component.png",
						selectedIconPath: "/static/icon/component_select.png",
						text: '花市',
						pagePath: "/pages/market/market",
					},
					{
						iconPath: "/static/icon/component.png",
						selectedIconPath: "/static/icon/component_select.png",
						text: '我的',
						pagePath: "/pages/my/my",
					},
				]
			}
		},
	}
</script>

<style lang='scss'>
	.checkpost {
		height: 800rpx;
		width: 375rpx;

		image {
			width: 80%;
		}
	}
</style>
